.sixthWrapper {
  min-height: 55%;
  width: 100%;
  background-color: #00ff87;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.sixthWrapper div:last-child {
  margin-bottom: 20px;
}
.sixthWrapper_element {
  width: 80%;
  min-height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #000;

}
.sixthWrapper_element span {
  background-color: #ccc;
}

.sixthWrapper_element__midle {
  margin: 20px auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #fff;
  border: 1px solid #000;
  width: 90%;
  min-height: 70px;
}

.sixthWrapper_element__midle div {
  margin: 1px;
  height: 70px;
  width: 70px;
  background-color: gray;
}